<template>
<div class="mobile-page project-list-container">
    <MainLayout :title="$t('Project.List')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content projects">
            <div class="project-filter">
                <InputSearch :search="fetchSearchProjects" />
                <template v-if="projects && projects.length">
                    <ViewComplete :statusComplete="statusComplete" :filter="filterComplete" />
                </template>
            </div>
            <div class="project-list">
                <template v-for="(project, index) in projects">
                    <ProjectItem :project="project" :key="index" />
                </template>
                <InfiniteLoading :isSearch="isSearch" :isError="isError" :isComplete="isComplete" :fetchData="fetchProjects" :classParent="'.project-list'" />
            </div>
            <div class="project-list" v-if="isFetchProjects && projects && !projects.length">
                <div class="no-results" slot="no-results">{{$t('Common.Loading.NoResult')}}</div>
            </div>
        </div>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import ProjectItem from '@/components/Home/NewProject/ProjectItem'
import ViewComplete from '@/components/Common/Button/ViewComplete'
import InputSearch from '@/components/Common/Input/InputSearch'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_PROJECTS,
    CREATE_PROJECTS
} from '@/mutations/Project.mutation'
import {
    PROJECT_STATUS,
} from '@/constants'

export default {
    name: 'ProjectList',
    components: {
        MainLayout,
        ProjectItem,
        ViewComplete,
        InputSearch,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isError: false,
            isComplete: false,
            isSearch: false,
            searchValue: '',
            statusComplete: false,
            isFetchProjects: null
        }
    },
    computed: {
        projects() {
            let prs = this.$store.state.Project.projects;
            if (this.statusComplete) {
                let result = prs.filter(pr => pr.status == PROJECT_STATUS.COMPLETE)
                return result;
            }
            return prs;
        },
        totalProject() {
            return this.$store.state.Project.totalProject;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_PROJECTS");
    },
    methods: {
        async fetchSearchProjects(searchValue = '') {
            await this.resetData();
            this.searchValue = searchValue;
            this.isSearch = true;
        },
        async fetchProjects() {
            try {
                this.page += 1;
                let params = {
                    pageNum: this.page + '',
                    searchValue: this.searchValue,
                }
                await this.$store.dispatch("FETCH_PROJECTS", params);
                this.isFetchProjects = true;

                if (this.totalProject == 0 || this.projects && this.projects.length >= this.totalProject) {
                    this.isComplete = true;
                }
                this.isSearch = false;
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
                this.isError = true;
            }
        },
        resetData() {
            this.$store.dispatch('CREATE_PROJECTS')
            this.page = 0;
            this.isComplete = false;
            this.isFetchProjects = false;
            this.isError = false;
            this.statusComplete = false;
        },
        filterComplete() {
            this.statusComplete = !this.statusComplete
        }
    },

    beforeDestroy() {
        this.searchValue = '';
        this.resetData();
    }
}
</script>

<style lang="scss">
.project-list-container {
    background-color: #E5E5E5;

    .projects {
        background-color: #E5E5E5;
    }

    .project-filter {
        position: absolute;
        width: 100%;
        z-index: 1;
    }

    .project-item-wrapper {
        background: #FFFFFF;
        border-radius: 0;
        margin: 1.5rem 0;

    }

    .project-list {
        padding-top: 8.1rem;
        max-height: 100%;
        overflow: auto;
    }

    .project-title {
        border-bottom: 0.05rem solid #E0E0E0;
        padding-bottom: 1rem;
    }
}
</style>
